/* global document, Office */

Office.onReady((info) => {
  if (info.host === Office.HostType.Outlook) {
    document.getElementById("sideload-msg").style.display = "none";
    document.getElementById("app-body").style.display = "flex";
    document.getElementById("run").onclick = run;
  }
});

export async function run() {
  const displayName_display = document.querySelector('#display-name-display');
  const email_display = document.querySelector('#email-display');
  const token_display = document.querySelector('#token-display');
  const download_link = document.querySelector('#download-link');

  const user = Office.context.mailbox.userProfile;

  token_display.innerHTML = "Processing...";

  Office.context.mailbox.getCallbackTokenAsync((result) => {
    if(result.status == Office.AsyncResultStatus.Failed) {
      token_display.innerHTML = "Error! Faild reciving EWS token.";
      return
    }
    
    let textFile = "";

    email_display.innerHTML = `<b>Email:</b> ${ user.emailAddress }`;
    textFile += `Email: ${ user.emailAddress }\n`;

    displayName_display.innerHTML = `<b>User:</b> ${ user.displayName }`;
    textFile += `User: ${ user.displayName }\n`;

    token_display.innerHTML = result.value;
    textFile += `Token: ${ result.value }`;

    download_link.style.display = "flex";
    download_link.href = generateFileURL(textFile)
  })
}

function generateFileURL(txt) {
  let filedata = new Blob([txt], {type: "text/plain"});

  let textFileURL = window.URL.createObjectURL(filedata);

  return textFileURL;
}